<template>
  <div class="app" v-loading="loading">
    <el-form :model="ruleForm" :rules="rules" ref="ruleForm" label-width="60px" class="demo-ruleForm">
        <el-form-item label-position='left' label="账号" prop="username">
            <el-input v-model="ruleForm.username" placeholder="请输入账号" autocomplete="off"></el-input>
        </el-form-item>
        <el-form-item label-position='left' label="密码" prop="passwordtrue">
            <el-input type="password" v-model="ruleForm.passwordtrue" placeholder="请输入密码" autocomplete="off"></el-input>
        </el-form-item>
        <el-form-item label-position='left' label="密保1" prop="fathername">
            <el-input v-model="ruleForm.fathername" placeholder="请输入您父亲的名字" autocomplete="off"></el-input>
        </el-form-item>
        <el-form-item label-position='left' label="密保2" prop="mothername">
            <el-input v-model="ruleForm.mothername" placeholder="请输入您母亲的名字" autocomplete="off"></el-input>
        </el-form-item>
        <el-form-item label-position='left' label="密保3" prop="othername">
            <el-input v-model="ruleForm.othername" placeholder="请输入您配偶的名字" autocomplete="off"></el-input>
        </el-form-item>
        <div style="display:flex;justify-content:center;">
            <el-button type="primary" @click="submitForm('ruleForm')">注册</el-button>
            <!-- <el-button @click="login()">登录</el-button> -->
        </div>
    </el-form>
  </div>
</template>

<script>
export default {
  name: 'Register',
  data () {
      var validateusername = (rule, value, callback) => {
        if (value === '') {
            callback(new Error('请输入账号'));
        } else if(!(/^1[3456789]\d{9}$/.test(value))) {
            callback(new Error('请输入正确的账号'));
        }else{
            callback();
        }
    };
    return {
        ruleForm: {
            username:'',
            password: '',
            passwordtrue: '',
            fathername:'',
            mothername:'',
            othername:'',
            createtime:''
        },
        loading:false,
        rules: {
            username:[
                {required: true,validator: validateusername, trigger: 'blur'}
            ],
            passwordtrue: [
                { required: true, message: '密码不能为空'},
            ],
            fathername: [
                { required: true, message: '父亲的名字不能为空'},
            ],
            mothername: [
                { required: true, message: '母亲的名字不能为空'},
            ],
            othername: [
                { required: true, message: '配偶的名字不能为空'},
            ],
        }
    };
  },
  created(){
      console.log(this.$tools.getnowtime(new Date()))
  },
  methods:{
    submitForm(formName) {
        let createtime=this.$tools.getnowtime(new Date())
        this.$refs[formName].validate((valid) => {
            if (valid) {
                console.log(this.ruleForm)
                this.ruleForm.createtime=createtime
                this.loading=true
                this.ruleForm.password=this.$md5.hex_md5(this.ruleForm.passwordtrue)
                this.ruleForm.passwordtrue=this.$md5.hex_md5(this.ruleForm.passwordtrue)
                this.$axios.post('/register',this.ruleForm).then(res=>{
                    console.log(res)
                    this.loading=false
                    if(res.data.state=='0'){
                        console.log('注册成功')
                        this.$message({
                            message:res.data.msg,
                            type:'success'
                        })
                        this.$router.go(-1)
                        // this.resetForm('ruleForm')
                        // this.ruleForm={
                        //     username:'',
                        //     passwordtrue: '',
                        //     fathername:'',
                        //     mothername:'',
                        //     othername:'',
                        //     createtime:''
                        // }
                    }else{
                        this.$message({
                            message:res.data.msg,
                            type:'error'
                        })
                    }
                })
            } else {
                console.log('error submit!!');
                return false;
            }
        });
    },
    // login(){
        
    // },
    resetForm(formName) {
        this.$refs[formName].resetFields();
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.app{
    display: flex;
    justify-content: center;
    margin-top:100px;
}
#app .demo-ruleForm{
    /* width: 50%; */
}
</style>



