<template>
  <div class="app" v-loading="loading">
    <el-form :model="ruleForm" :rules="rules" ref="ruleForm" label-width="60px" class="demo-ruleForm">
        <el-form-item label-position='left' label="账号" prop="username">
            <el-input v-model="ruleForm.username" placeholder="请输入账号" autocomplete="off"></el-input>
        </el-form-item>
        <el-form-item label-position='left' label="密保1" prop="fathername">
            <el-input v-model="ruleForm.fathername" placeholder="请输入您父亲的名字" autocomplete="off"></el-input>
        </el-form-item>
        <el-form-item label-position='left' label="密保2" prop="mothername">
            <el-input v-model="ruleForm.mothername" placeholder="请输入您母亲的名字" autocomplete="off"></el-input>
        </el-form-item>
        <el-form-item label-position='left' label="密保3" prop="othername">
            <el-input v-model="ruleForm.othername" placeholder="请输入您配偶的名字" autocomplete="off"></el-input>
        </el-form-item>
        <!-- <el-form-item label-position='left' label="密码" prop="password">
            <el-input type="password" v-model="ruleForm.password" placeholder="请输入密码" autocomplete="off"></el-input>
        </el-form-item> -->
        <div style="display:flex;justify-content:center;">
            <el-button type="primary" @click="submitForm('ruleForm')">设置密码</el-button>
            <!-- <el-button @click="login()">登录</el-button> -->
        </div>
    </el-form>
    <el-dialog title="设置密码" :visible.sync="dialogVisible">
        <el-form style="width:50%;margin:0 auto;" :model="passForm" :rules="passRule" ref="passForm" label-width="60px" class="demo-ruleForm">
            <div>
                <el-form-item label-position='left' label="密码" prop="passwordtrue">
                    <el-input v-model="passForm.passwordtrue" type="password" placeholder="请输入密码" autocomplete="off"></el-input>
                </el-form-item>
                <div slot="footer" class="dialog-footer" style="display:flex;justify-content:center;">
                    <el-button type="primary" @click="submitPassForm('passForm')">更换密码</el-button>
                </div>
            </div>
        </el-form>
    </el-dialog>
  </div>
</template>

<script>
export default {
  name: 'ForgetPass',
  data () {
      var validateusername = (rule, value, callback) => {
        if (value === '') {
            callback(new Error('请输入账号'));
        } else if(!(/^1[3456789]\d{9}$/.test(value))) {
            callback(new Error('请输入正确的账号'));
        }else{
            callback();
        }
    };
    return {
        ruleForm: {
            username:'',
            fathername:'',
            mothername:'',
            othername:'',
        },
        passForm: {
            username:'',
            password:'',
            passwordtrue:''
        },
        loading:false,
        rules: {
            username:[
                {required: true,validator: validateusername, trigger: 'blur'}
            ],
            fathername: [
                { required: true, message: '父亲的名字不能为空', trigger: 'blur'},
            ],
            mothername: [
                { required: true, message: '母亲的名字不能为空', trigger: 'blur'},
            ],
            othername: [
                { required: true, message: '配偶的名字不能为空', trigger: 'blur'},
            ],
        },
        passRule:{
            passwordtrue: [
                { required: true, message: '新密码不能为空', trigger: 'blur'},
            ]
        },
        dialogVisible:false
    };
  },
  created(){
      
  },
  methods:{
    submitPassForm(formName){
        this.$refs[formName].validate((valid) => {
            if (valid) {
                console.log(this.passForm)
                this.passForm.username=this.ruleForm.username
                this.passForm.password=this.$md5.hex_md5(this.passForm.passwordtrue)
                this.$axios.post('/resetPass',this.passForm).then(res=>{
                    console.log(res)
                    if(res.data.state=='0'){
                        this.$message({
                            message:res.data.msg,
                            type:'success'
                        })
                        this.dialogVisible=false
                    }else{
                        this.$message({
                            message:res.data.msg,
                            type:'error'
                        })
                    }
                    this.passForm.password=''
                })
            }
        })
    },
    submitForm(formName) {
        this.$refs[formName].validate((valid) => {
            if (valid) {
                console.log(this.ruleForm)
                this.loading=true
                this.$axios.post('/validateQues',this.ruleForm).then(res=>{
                    this.loading=false
                    console.log(res)
                    if(res.data.state=='0'){
                        this.dialogVisible=true
                        this.passForm.password=''
                    }else{
                        this.$message({
                            message:res.data.msg,
                            type:'error'
                        })
                    }
                })
            } else {
                console.log('error submit!!');
                return false;
            }
        });
    },
    login(){
        this.$router.go(-1)
    },
    resetForm(formName) {
        this.$refs[formName].resetFields();
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.app{
    display: flex;
    justify-content: center;
    margin-top:100px;
}
#app .demo-ruleForm{
    /* width: 50%; */
}
</style>



